<template>
    <div class="examples">
        <div class="title-wraper" @click="disabled1=false,disabled2=false">
            <div class="title-c">|&nbsp;&nbsp;产品手册定制中心&nbsp;&nbsp;|</div>
            <div class="block-wraper">
                <div class="block"></div>
                <div class="block"></div>
                <div class="block"></div>
            </div>
        </div>
            <van-row>
                <van-col span="4" class="nav" @click="disabled1=false,disabled2=false">
                    <ul>
                        <li v-for="item in selectOptions1" :key="item.id" @click="handClick(item,item.id)" :class="item.style" >
                            <van-icon class-prefix="icon" class="nav-icon iconfont" :name="item.icon" :info="collectNumber"/>
                            <div class="navname">{{item.firstName}}</div>
                        </li>
                        <li v-for="item in selectOptions2" :key="item.id" @click="handClick(item,item.id)" :class="item.style">
                            <van-icon class-prefix="icon" class="nav-icon iconfont" :name="item.icon" />
                            <div class="navname">{{item.firstName}}</div>
                        </li>
                    </ul>
                </van-col>
                <van-col span="20" v-if="secondClass" class="secondClass" @click="disabled1=false,disabled2=false">
                    <div v-for="item in secondItem" :key="item.id" style="position: relative;">
                        <van-image
                            :src="item.secondImg"
                            @click="selectProduct(item,item.id)"
                            class="content-wraper"
                        />
                        <div class="content">
                            <div class="text">{{item.secondName}}</div>
                            <div class="detail">{{item.secondEnglishName}}</div>
                        </div>
                    </div>
                    <div style="height: 30px;background-color: white"></div>
                </van-col>

                <van-col span="20" v-if="thirdClass" class="thirdClassbig">

                    <van-search
                        v-if='search'
                        v-model="searchcontent"
                        show-action
                        shape="round"
                        @search="onSearch"
                        @click="disabled1=false,disabled2=false"
                    >
                        <div slot="action" @click="onSearch" class="searchButton">搜索</div>
                    </van-search>

                    <div v-if="screen" class="select-wrapper">
                        <div style="width: 100%;height: 11.2vw;" v-for="(item,index) in condition">
                                <van-icon  name="cross" @click="deletecondition(index)" class="cross" v-if="cross"/>
                            <div class="select" round type="info">
                                <van-button  @click="selectClick1(index)">
                                    <van-icon class="iconfont" class-prefix='icon' name="chanpin-mianxing" /> <spam style="position:relative;left:5vw;">{{item.data1.text}}</spam></van-button>
                                <span style="color: white">|</span>
                                <van-button  @click="selectClick2(index)">
                                    <span style="position:relative;right:5vw;">
                                        {{item.data2.text.length > 5 ? (item.data2.text).slice(0,4)+'...':(item.data2.text)}}
                                    </span>
                                    <van-icon class="iconfont" class-prefix='icon' name="canshuguanli" /></van-button>
                            </div>
                        </div>
                        <van-icon class-prefix="icon" class="iconfont plus" name="tianjia" @click="addcondition" v-if="add"/>
                    </div>

                    <van-picker :item-height="itemheight" :default-index="default1" v-if="disabled1" :columns="columns" @change="onChange1" :class="picker"/>
                    <van-picker :item-height="itemheight" :default-index="default2" v-if="disabled2" :columns="columns" @change="onChange2" :class="picker" />

                    <div class="mini-refresh-wrapper" :class="showtop" @click="disabled1=false,disabled2=false" >
                        <minirefresh id="minirefresh"
                                     ref="minirefresh"
                                     :options="options"
                                     @pullingDown="onPullingDown"
                                     @pullingUp="onPullingUp"
                                     >
                            <div slot="content" id="listdata" class="imgcontent data-list" @click="disabled1=false,disabled2=false">
                                <div v-for="(item, index) in thirdItem" :key="index + '1'" class="imgdetail" v-show="filter">
                                    <van-image
                                            class=""
                                            :src="item.thirdImg"
                                            @click="searchDetail(item)"
                                    />
                                    <div class="shoucang">
                                        <van-icon class-prefix="icon" class="iconfont" name="shoucang" :color="item.isCollect === 1 ?'#FFA500':'grey'" @click="Collect(item.id)"/>
                                    </div>
                                    <!-- secondId 为1, 代表是工业胶管-->
                                    <div class="info-wraper" v-if="item.secondId === 1">
                                        <div class="text">
                                            {{item.typeOne.productName}}
                                        </div>
                                        <div class="detail">
                                            <div class="desc"> 内径: {{item.typeOne.hoseId }} Inch </div>
                                            <div class="desc"> 外径: {{item.typeOne.hoseOd }}mm</div>
                                        </div>
                                        <van-icon class-prefix="icon" class="iconfont van-icon" name="tianjia" @click="addClick(item.id)" :color="item.add ? 'grey' : '#FFA500'"/>
                                    </div>
                                    <!-- secondId 为2, 代表是液压胶管-->
                                    <div class="info-wraper" v-else-if="item.secondId === 2">
                                        <div class="text">
                                            <b>{{item.typeTwo.partNumber}}</b>
                                        </div>
                                        <div class="detail">
                                            <div class="desc"> 内径: {{item.typeTwo.hoseId }} Inch </div>
                                            <div class="desc"> 压力: {{item.typeTwo.wpMpa }}</div>
                                        </div>
                                        <van-icon class-prefix="icon" class="iconfont van-icon" name="tianjia" @click="addClick(item.id)" :color="item.add ? 'grey' : '#FFA500'"/>
                                    </div>

                                    <div class="info-wraper" v-else>
                                        <div class="text">
                                            {{item.title}}
                                        </div>
                                        <div class="detail">
                                            <div class="desc desc-guidao">{{item.describe }}</div>
                                        </div>
                                        <van-icon class-prefix="icon" class="iconfont van-icon" name="tianjia" @click="addClick(item.id)" :color="item.add ? 'grey' : '#FFA500'"/>
                                    </div>
                                </div>
                                <div v-for="(item, index) in thirdItem" :key="index + '2'" class="imgdetail" v-show="product">
                                    <van-image
                                            class=""
                                            :src="item.thirdImg"
                                            @click="searchDetail(item)"
                                    />
                                    <!-- secondId 为1, 代表是工业胶管-->
                                    <div class="info-wraper" v-if="item.secondId === 1">
                                        <div class="textpro">
                                            {{item.typeOne.productName}}
                                        </div>
                                    </div>
                                    <!-- secondId 为2, 代表是液压胶管-->
                                    <div class="info-wraper" v-else-if="item.secondId === 2">
                                        <div class="textpro">
                                            <b>{{item.typeTwo.partNumber}}</b>
                                        </div>
                                    </div>

                                    <div class="info-wraper" v-else>
                                        <div class="text">
                                            {{item.title}}
                                        </div>
                                        <div class="detail">
                                            <div class="desc desc-guidao">{{item.describe }}</div>
                                        </div>
                                        <van-icon class-prefix="icon" class="iconfont van-icon" name="tianjia" @click="addClick(item.id)" :color="item.add ? 'grey' : '#FFA500'"/>
                                    </div>
                                </div>
                            </div>

                        </minirefresh>
                    </div>

                </van-col>
            </van-row>
            <van-goods-action @click="disabled1=false,disabled2=false">
                <van-goods-action-button @click="showSelected">
                    <van-icon :info="result.length" class-prefix="icon" class="iconfont" name="renwuqingdan"/><span>已选择</span>
                </van-goods-action-button>
                <van-goods-action-button text="下一步 >" @click="goNext"/>
            </van-goods-action>
            <van-popup
                v-model="show"
                round
                position="bottom"
                :style="{ height: '50%',backgroundColor:'black',color:'white'}"
            >
                <div class="selectButton">
                    <span class="selectAll" @click="selectAll"><u>全选</u></span>
                    <span class="selectNotAll" @click="selectNotAll"><u>全不选</u></span>
                </div>
                <van-checkbox-group v-model="result" checked-color="#07c160">
                <van-cell-group v-for="(item,index) in products" :key="index + '3'">
                        <van-cell
                            :key="item.id"
                            :style="{backgroundColor:'black',color:'white'}"
                            @click="toggle(index)"
                            :border="border"
                        >
                            <div class="title" v-if="item.secondId === 1">
                                {{item.typeOne.productName}}
                                <span style="color: grey;"> - 内径: {{item.typeOne.hoseId }} Inch - 外径: {{item.typeOne.hoseOd }}mm </span>
                            </div>
                            <div class="title" v-else-if="item.secondId === 2">
                                {{item.typeTwo.partNumber}}
                                <span style="color: grey;"> - 内径: {{item.typeTwo.hoseId }} Inch - 压力: {{item.typeTwo.wpMpa }}</span>
                            </div>
                            <div class="title" v-else>
                                {{item.title}} / <span style="color: grey;"> {{ item.describe }} </span>
                            </div>
                            <van-checkbox
                                v-model="item.checked"
                                :name="item.thirdName"
                                ref="checkboxes"
                                :label-disabled="label"
                            >
                                <van-icon
                                    slot="icon"
                                    slot-scope="item"
                                    class-prefix="icon"
                                    class="iconfont icon-custom"
                                    :name="item.checked ? 'xuanzehou' : 'xuanzekuang'"
                                    color="white"/>
                            </van-checkbox>
                        </van-cell>
                    </van-cell-group>
                </van-checkbox-group>
                <van-goods-action>
                <van-goods-action-button @click="showSelected">
                    <van-icon :info="result.length" class-prefix="icon" class="iconfont" name="renwuqingdan"/><span>已选择</span>
                </van-goods-action-button>
                <van-goods-action-button text="下一步 >" @click="goNext"/>
            </van-goods-action>
            </van-popup>
        </div>
</template>
<script>
    import '../../style/iconfont.css';
    import { getFirstDirectory, getSecondDirectory, getThirdDirectory, getSearch, getAllInfos, getPageThrid, getAllcollect, changeCollect, addClickNum} from '@/api/directory'
    import { BASE_API } from '@/constant';
    import MiniRefresh from '../../components/minirefresh/minirefresh.vue';
    import { mapGetters } from 'vuex';
    import {TreeSelect,
             Row,
             Col,
             Image,
             GoodsAction,
             GoodsActionIcon,
             GoodsActionButton,
             DropdownMenu,
             DropdownItem,
             Picker,
             Icon,
             Search,
             Button,
             Popup,
             Checkbox,
             CheckboxGroup,
             Cell,
             CellGroup,
             ImagePreview,
             Toast
            } from 'vant';

    export default {
        components: {
            [Row.name]: Row,
            [Col.name]: Col,
            [TreeSelect.name]: TreeSelect,
            [Image.name]: Image,
            [GoodsAction.name]: GoodsAction,
            [GoodsActionIcon.name]: GoodsActionIcon,
            [GoodsActionButton.name]: GoodsActionButton,
            [DropdownMenu.name]: DropdownMenu,
            [DropdownItem.name]: DropdownItem,
            [Picker.name]: Picker,
            [Icon.name]: Icon,
            [Search.name]: Search,
            [Button.name]: Button,
            [Popup.name]: Popup,
            [Checkbox.name]: Checkbox,
            [CheckboxGroup.name]: CheckboxGroup,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [ImagePreview.name]: ImagePreview,
            [Toast.name]: Toast,
            minirefresh: MiniRefresh,

        },
        data() {
            return {
                showtop: '',
                minirefresh: '',
                picker: '',
                isfilter: false,
                product: false,
                filter: false,
                index: 0,
                cross: false,
                add: true,
                condition: [{data1: {text: '产品特性'},
                             data2: {text: '参数选择'}
                            }],
                text: [],
                key: [],
                collectNumber: 0,
                thirdClass: false,
                secondClass: true,
                top: true,
                screen: true,
                default2:0,
                default1:0,
                itemheight: 44,
                disabled1: false,
                disabled2: false,
                label: true,
                show: false,
                loading: false,
                border:false,
                checked: true,
                list: [],
                selectOptions2: [],
                columns: [],
                searchcontent: '',
                search: false,
                selectOptions1: [{id:"collect",firstName: '我的收藏',icon:"shoucangjia",style:'selectUl'},
                                {id:"search",firstName: '快捷搜索',icon:"sousuo",style:'selectUl'},
                ],
                activeId: 1,
                activeIndex: 0,
                secondItem: [],
                thirdItem: [],
                type:Object,
                pagination: {
                    page: 1,
                    pageSize: 10,
                    total: 0
                },
                secondId: null,
                options: {
                    container: '#minirefresh',
                    up: {
                        isAuto: true,
                        loadFull: {
                            isEnable: true
                        }
                    }
                }
            }
        },

        computed: {
            // 使用对象展开运算符将 getter 混入 computed 对象中
            ...mapGetters([
                'products'
            ]),
            result:{
                get(){
                    return this.$store.state.app.result
                },
                set(val){
                    this.$store.dispatch('app/setResult', val);
                }
            }
        },

        created() {
            this.getData()
            getAllcollect().then(res=>{
                if (res.status === 200) {
                    this.collectNumber = res.data.total
                }
            })
        },


        methods: {
            onPullingDown() {
                if (this.id === "search") {
                        this.getSearchDirectoryData('down', true)
                } else if (this.id === "collect") {
                        this.getCollect('down', true)
                } else if (this.id === 2 || this.isfilter === false) {
                        this.getThirdDirectoryData('down', true);
                    } else {
                        this.getPage('down', true)
                    }
                },
            onPullingUp() {
                if (this.id === "search") {
                        this.getSearchDirectoryData("up")
                    } else if (this.id === "collect") {
                        this.getCollect("up")
                    } else if (this.id === 2 || this.isfilter === false) {
                        this.getThirdDirectoryData('up');
                    } else {
                        this.getPage('up')
                    }
            },
            getPage(pull, isReset) {
                // this.text = JSON.stringify(this.text)
                // this.key = JSON.stringify(this.key)
                if (isReset) {
                    this.pagination.page = 1;
                }
                const page = this.pagination.page
                const postData = new FormData()
                postData.append('secondId',  this.secondId)
                postData.append('size', 10)
                postData.append('page',  this.pagination.page)
                postData.append('values',  this.text)
                postData.append('keys',  this.key)
                getPageThrid(postData).then(res => {
                    this.filter = true
                    this.product = false
                    if (res.status === 200) {
                        if (res.data.data && res.data.data.length >= 0) {
                            let items = res.data.data;
                            items.forEach(value => {
                                return value.thirdImg = BASE_API + value.thirdImg
                            })
                            for (let i = 0; i < items.length; i++) {
                                for (let j = 0; j < this.products.length; j++) {
                                    if (items[i].id == this.products[j].id) {
                                        items[i].add = "yes"
                                    }
                                }
                            }
                            if (pull === 'down') {
                                this.thirdItem = items;
                                this.$refs.minirefresh.endDownLoading(true);
                            } else {
                                if (page === 1) {
                                    this.thirdItem = items;
                                } else {
                                    this.thirdItem = this.thirdItem.concat(items);
                                }
                                this.$refs.minirefresh.endUpLoading(this.thirdItem.length >= res.data.totalCount);
                            }
                        }
                        this.pagination.page = this.pagination.page + 1;
                        this.pagination.pageSize = res.data.size;
                        this.pagination.total = res.data.totalCount;
                        }
                })
            },
            getThirdDirectoryData(pull, isReset) {
                if (this.secondId ===1 || this.secondId ===2) {
                    this.condition = [{data1: {text: '产品特性'},
                        data2: {text: '参数选择'}
                    }],
                    this.cross = false
                    this.filter = false
                    this.product = true
                    if (isReset) {
                        this.pagination.page = 1;
                    }
                    const page = this.pagination.page
                    getThirdDirectory({
                        pageSize: 10,
                        page: this.pagination.page,
                        secondId: this.secondId,
                    }).then(res => {
                        if (res.status === 200) {
                            let items = res.data.list;
                                items.forEach(value => {
                                    return value.thirdImg = BASE_API + value.thirdImg
                                })
                                this.thirdItem = items;
                            if (pull === 'down') {
                                this.thirdItem = items;
                                this.$refs.minirefresh.endDownLoading(true);
                            } else {
                                if (page === 1) {
                                    this.thirdItem = items;
                                } else {
                                    this.thirdItem = this.thirdItem.concat(items);
                                }
                            }
                            this.$refs.minirefresh.endUpLoading(this.thirdItem.length >= res.data.total);
                            // this.pagination.page = this.pagination.page + 1;
                            this.pagination.pageSize = res.data.pageSize;
                            this.pagination.total = res.data.total;
                        }
                    })
                } else{
                    const page = this.pagination.page;
                    this.filter = true
                    this.product = false
                    if (isReset) {
                        this.pagination.page = 1;
                    }
                    getThirdDirectory({
                        secondId: this.secondId,
                        pageSize: 10,
                        page: this.pagination.page
                    }).then(res => {
                        if (res.status === 200) {
                                let items = res.data.list || [];
                                items.forEach( value => {
                                    return value.thirdImg = BASE_API + value.thirdImg
                                })
                                for (let i = 0; i < items.length; i++) {
                                    for (let j = 0; j < this.products.length; j++){
                                        if (items[i].id == this.products[j].id) {
                                            items[i].add = "yes"
                                        }
                                    }
                                }
                            if (pull === 'down') {
                                this.thirdItem = items;
                                this.$refs.minirefresh.endDownLoading(true);
                            } else {
                                if (page === 1) {
                                    this.thirdItem = items;
                                } else {
                                    this.thirdItem = this.thirdItem.concat(items);
                                }
                            }
                            this.$refs.minirefresh.endUpLoading(this.thirdItem.length >= res.data.total);
                            this.pagination.page = this.pagination.page + 1;
                            this.pagination.pageSize = res.data.pageSize;
                            this.pagination.total = res.data.total;
                        }
                    })
                }
            },
            getCollect(pull, isReset) {
                if (isReset) {
                    this.pagination.page = 1;
                }
                const page = this.pagination.page;
                getAllcollect({
                    pageSize: 10,
                    page: this.pagination.page
                }).then(res => {
                    if (res.status === 200) {
                        let items = res.data.list || [];
                        items.forEach( value => {
                            return value.thirdImg = BASE_API + value.thirdImg
                        })
                        for (let i = 0; i < items.length; i++) {
                            for (let j = 0; j < this.products.length; j++){
                                if (items[i].id == this.products[j].id) {
                                    items[i].add = "yes"
                                }
                            }
                        }
                        if (pull === 'down') {
                            this.thirdItem = items;
                            this.$refs.minirefresh.endDownLoading(true);
                        } else {
                            if (page === 1) {
                                this.thirdItem = items;
                            } else {
                                this.thirdItem = this.thirdItem.concat(items);
                            }
                            this.$refs.minirefresh.endUpLoading(this.thirdItem.length >= res.data.total);
                        }

                        this.pagination.page = this.pagination.page + 1;
                        this.pagination.pageSize = res.data.size;
                        this.pagination.total = res.data.total;
                    }

                })
            },
            getSearchDirectoryData(pull, isReset) {
                if (isReset) {
                    this.pagination.page = 1;
                }

                const page = this.pagination.page;

                getSearch({
                    thirdName:this.searchcontent,
                    pageSize: 10,
                    page: this.pagination.page
                }).then(res => {
                    if (res.status === 200) {
                        let items = res.data.list || [];
                        items.forEach( value => {
                            return value.thirdImg = BASE_API + value.thirdImg
                        })
                        for (let i = 0; i < items.length; i++) {
                            for (let j = 0; j < this.products.length; j++){
                                if (items[i].id == this.products[j].id) {
                                    items[i].add = "yes"
                                }
                            }
                        }
                        if (pull === 'down') {
                            this.thirdItem = items;
                            this.$refs.minirefresh.endDownLoading(true);
                        } else {
                            if (page === 1) {
                                this.thirdItem = items;
                            } else {
                                this.thirdItem = this.thirdItem.concat(items);
                            }
                            this.$refs.minirefresh.endUpLoading(this.thirdItem.length >= res.data.total);
                        }

                        this.pagination.page = this.pagination.page + 1;
                        this.pagination.pageSize = res.data.pageSize;
                        this.pagination.total = res.data.total;
                    }
                })
            },
            getData() {
                getFirstDirectory().then( res => {
                    if (res.status === 200) {
                        this.selectOptions2 = res.data
                    }
                    const selectedOptions2 = this.selectOptions2
                    for (let i = 0; i < selectedOptions2.length; i++) {
                     selectedOptions2[i].style = "selectUl"
                     this.selectOptions2 = selectedOptions2
                    }
                })
            },
            onChange1(picker, value,index) {
                if (value.text === '') {
                    value.text = '产品特性'
                }
                this.condition[this.index].data1= value
                this.condition[this.index].data2 = {text: '参数选择'}
                this.default1 = parseInt(index)
                this.default2 = 0
                // this.key = this.data1.key
            },
            onChange2(picker, value,index) {
                if (value.text === '') {
                    value.text = '参数选择'
                }
                this.key = []
                this.text = []
                this.thirdItem = []
                this.pagination.page = 1
                this.condition[this.index].data2= value
                this.default2 = parseInt(index)
                // this.text = this.data2.text
                for (let i = 0;i < this.condition.length; i++) {
                    if (this.condition[i].data2.text !== '参数选择') {
                        this.text.push(this.condition[i].data2.text)
                    }
                }
                for (let i = 0;i < this.condition.length; i++) {
                    if (this.condition[i].data2.text !== '参数选择') {
                        this.key.push(this.condition[i].data1.key)
                    }
                }
                this.isfilter = true
                this.onPullingDown()
            },
            selectClick1(index) {
                this.default1 = 0
                const number = index + 1
                this.picker = 'picker' + number
                this.index = index
                this.disabled2 = false
                const disabled1 = this.disabled1
                this.disabled1 = !disabled1
                this.columns = [{text:""}].concat(this.type.productFeatures);
                for (let i = 0;i < this.condition.length; i++) {
                    if (this.condition[i].data2.text !== '参数选择') {
                        for (let j = 1; j < this.columns.length; j++) {
                            if (this.columns[j].key === this.condition[i].data1.key && i!==index) {
                               this.columns.splice(j,1)
                            }
                        }

                    }
                }
                for (let i = 0; i < this.columns.length ; i++) {
                    if (this.columns[i].text === this.condition[index].data1.text){
                        this.default1 = i
                    }
                }
            },
            selectClick2(index) {
                const number = index + 1
                this.picker = 'picker' + number
                this.index = index
                if (this.condition[index].data1.text === '产品特性') {
                    return;
                }
                const type = this.type;
                const disabled2 = this.disabled2;
                this.disabled1 = false;
                this.disabled2 = !disabled2;
                this.columns = [{text:""}].concat(type[this.condition[index].data1.key]);

            },
            selectProduct(item,id) {
                const params = {secondId:id}
                getAllInfos(params).then(res=>{
                    if (res.status === 200) {
                        this.columns = res.data.productFeatures
                        this.type = res.data
                        this.default1 = 0
                        this.default2 = 0
                    }
                })
                this.secondId = id;
                this.secondClass = false
                this.thirdClass = true
            },
            onSearch() {
                this.pagination.page = 1
                this.thirdItem = []
                this.getSearchDirectoryData()
            },
            addClick(id) {
                const item = this.thirdItem
                for (let i = 0; i < item.length; i++) {
                    if (item[i].id == id && !item[i].add) {
                        let temp = [].concat(this.products)
                        temp.push(item[i])
                        this.result.push(item[i].thirdName)
                        this.$store.dispatch('app/setProducts', temp);
                        item[i].add = "yes"
                        this.thirdItem = item
                    }
                }
            },
            Collect(id) {
                const item = this.thirdItem
                const params = {thirdId:id}
                for (let i = 0; i < item.length; i++) {
                    if (item[i].id == id) {
                        if (item[i].isCollect === 1){
                            item[i].isCollect = 0
                        } else {
                            item[i].isCollect = 1
                        }
                    }
                    this.thirdItem = item
                }
                changeCollect(params).then(res=>{
                    if (res.status === 200) {
                        // this.onPullingDown()
                        getAllcollect().then(res=>{
                            if (res.status === 200) {
                                this.collectNumber = res.data.total
                            }
                        })
                    }
                })
            },

            handClick(item, id){
                this.data2 = {text: '参数选择'}
                this.data1 = {text: '产品特性'}
                this.pagination.page = 1
                this.thirdItem = []
                this.id = id
                this.searchcontent=""
                this.showtop = ''
                this.screen = false
                this.search = false
                const selectedOptions1 = this.selectOptions1
                const selectedOptions2 = this.selectOptions2
                selectedOptions1.style = "selectUl"
                selectedOptions2.style = "selectUl"
                for (let i = 0; i < selectedOptions1.length; i++) {
                    if (selectedOptions1[i].id == id) {
                        selectedOptions1[i].style = "selectedUl"
                        this.selectOptions1 = selectedOptions1
                    }
                }
                for (let i = 0; i < selectedOptions1.length; i++) {
                    if (selectedOptions1[i].id !== id) {
                        selectedOptions1[i].style = "selectUl"
                        this.selectOptions1 = selectedOptions1
                    }
                }
                for (let i = 0; i < selectedOptions2.length; i++) {
                    if (selectedOptions2[i].id == id) {
                        selectedOptions2[i].style = "selectedUl"
                        this.selectOptions2 = selectedOptions2
                    }
                }
                for (let i = 0; i < selectedOptions2.length; i++) {
                    if (selectedOptions2[i].id !== id) {
                        selectedOptions2[i].style = "selectUl"
                        this.selectOptions2 = selectedOptions2
                    }
                }
                if (id===1||id===2) {
                    const parms = {firstId:item.id}
                    getSecondDirectory(parms).then(res=>{
                        if (res.status === 200) {
                            this.secondItem =res.data
                            this.secondItem.forEach( value => {
                                return value.secondImg = BASE_API + value.secondImg
                            })
                        }
                    })

                    this.secondClass = true
                    this.thirdClass = false
                    this.top = true
                    if (id === 1) {
                        this.isfilter = false
                        this.screen = true
                        this.showtop = 'showtop1'
                    }
                    if (id === 2) {
                        this.screen = false
                        this.top = false
                    }
                }
                if (id === "search") {
                    this.filter = true
                    this.product = false
                    // this.getSearchDirectoryData()
                    this.secondClass = false
                    this.thirdClass = true
                    this.showtop = 'showtop1'
                    this.search = true
                    this.top = true
                    this.onPullingDown()
                }
                if (id === "collect") {
                    this.filter = true
                    this.product = false
                    // this.getSearchDirectoryData()
                    this.secondClass = false
                    this.thirdClass = true
                    this.top = false
                    this.onPullingDown()
                }

            },
            addcondition() {
                this.disabled1=false
                this.disabled2=false
                for (let i = 0; i< this.condition.length; i++) {
                    if (this.condition[i].data2.text === '参数选择'){
                        return

                    }
                }
                this.condition.push({data1: {text: '产品特性'},
                    data2: {text: '参数选择'},})
                this.showtop = 'showtop' + this.condition.length
                if (this.condition.length ===6) {
                    this.add = false
                } else {
                    this.add = true
                }
                if (this.condition.length ===1) {
                    this.cross = false
                } else {
                    this.cross = true
                }
            },
            deletecondition(index) {
                this.disabled1=false
                this.disabled2=false
                this.condition.splice(index,1)
                this.showtop = 'showtop' + this.condition.length
                if (this.condition.length ===1) {
                    this.cross = false
                } else {
                    this.cross = true
                }
                if (this.condition.length ===6) {
                    this.add = false
                } else {
                    this.add = true
                }
                this.key = []
                this.text = []
                for (let i = 0;i < this.condition.length; i++) {
                    if (this.condition[i].data2.text !== '参数选择') {
                        this.text.push(this.condition[i].data2.text)
                    }
                }
                for (let i = 0;i < this.condition.length; i++) {
                    if (this.condition[i].data2.text !== '参数选择') {
                        this.key.push(this.condition[i].data1.key)
                    }
                }
                this.getPage()
                this.isfilter = true
            },
            showSelected() {
              this.show = !this.show
              this.disabled1=false,
              this.disabled2=false
            },
            goNext() {
                if (this.result.length === 0) {
                    return
                } else {
                    this.$router.push({path: '/information'});
                    this.disabled1=false;
                    this.disabled2=false;
                }
            },
            toggle(index) {
                this.$refs.checkboxes[index].toggle();
            },
            selectAll(){
                if(this.result.length!==this.products.length){
                    for(let i=0;i<this.products.length;i++){
                        this.$refs.checkboxes[i].toggle(true);
                    }
                }
            },
            selectNotAll(){
                if(this.result.length!==0){
                    for(let i=0;i<this.products.length;i++){
                        this.$refs.checkboxes[i].toggle(false);
                    }
                }
            },
            async searchDetail(item) {
                const toast = Toast.loading({
                    duration: 0, // 持续展示 toast
                    forbidClick: true,
                    mask: true,
                    message: '加载中。。。'
                });
                const images = [BASE_API + item.pdfImg];
                Toast.clear();
                ImagePreview(images);
                const params = {
                    thirdName : item.thirdName
                }
                addClickNum(params).then(res => {
                    if (res.status === 200){
                        // TODO
                    }
                })
            }
        }
    }
</script>
