import request from '@/https'

export function getFirstDirectory(params) {
    return request({
        url: '/firstLevel/getAllDirectory',
        method: 'get',
        params
    })
}

export function getSecondDirectory(params) {
    return request({
        url: '/secondlevel/getAllSecondDirectoryByFirstId',
        method: 'get',
        params
    })
}

export function getThirdDirectory(params) {
    return request({
        url: '/thirdLevel/getAllThirdLevelDirectoryBySecondId',
        method: 'get',
        params
    })
}

export function getDetail(params) {
    return request({
        url: '/typeOne/selectTypeOneById',
        method: 'post',
        params
    })
}

export function getSearch(params) {
    return request({
        url: '/thirdLevel/selectThirdLevelDirectoryByThirdName',
        method: 'get',
        params
    })
}

export function getAllInfos(params) {
    return request({
        url: '/product/getAllInfos',
        method: 'get',
        params
    })
}

export function getAllcollect(params) {
    return request({
        url: '/thirdLevel/selectCollectDirectory',
        method: 'get',
        params
    })
}

export function changeCollect(params) {
    return request({
        url: '/thirdLevel/insertCollect',
        method: 'get',
        params
    })
}

export function getPageThrid(data) {
    return request({
        url: '/product/getPageThrid',
        method: 'post',
        data
    })
}

export function addClickNum(params) {
    return request({
        url: '/thirdLevel/addClickNum',
        method: 'get',
        params
    })
}

